@charset "utf-8";
@import "all.sass";


.wrapper {
  width: 84.375%;
  margin: auto; }
figure.title-icon {
  width: 3.4375%;
  font-size: 0;
  margin: auto; }
.title {
  padding: 2% 0;
  font-weight: bold;
  text-align: center;
  @include fontH1; }


section.leading-page {
  display: none;
  width: 100%;
  height: 100vh;
  position: relative;
  figure.logo {
    width: 15%;
    padding: 5.5% 6.25%; }
  .leading-page-box {
    width: 52%;
    @include pstc5;
    figure.product {
      width: 100%; }
    p {
      @include fontH1;
      text-align: center;
      font-weight: bold; }
    .btn-box {
      padding-top: 15%;
      font-size: 0;
      .btn {
        width: 45%;
        margin: 0 2.5%;
        color: #fff;
        background-color: #000;
        text-align: center;
        cursor: pointer;
        @include dib;
        @include fontH1; } } } }


section.banner {
  position: relative;
  figure.logo {
    width: 15%;
    padding: 5.5% 6.25%;
    position: absolute;
    top: 0;
    left: 0; }
  figure.cow {
    width: 87.03125%;
    margin-bottom: -13%;
    float: right; }
  .slogan-box {
    clear: both;
    width: 82.03125%;
    margin: auto;
    position: relative;
    figure.pic-1 {
      width: 100%; }
    figure.pic-2 {
      width: 89.523809%;
      position: absolute;
      top: 0;
      right: -30%; } }
  .product-box {
    width: 100%;
    margin-top: -5%;
    font-size: 0;
    figure.pic-3 {
      width: 28.59375%;
      padding-top: 8%;
      padding-left: 11.40625%;
      box-sizing: border-box;
      display: inline-block;
      vertical-align: top; }
    figure.pic-4 {
      width: 71.40625%;
      padding-right: 1%;
      box-sizing: border-box;
      display: inline-block;
      vertical-align: top; } }
  .slogan-txt {
    @include fontH3;
    font-weight: bold;
    padding-left: 11.40625%;
    margin-top: -10%; } }


section.video,section.teaching {
  padding: 5% 0;
  .wrapper {
    .video-carousel {
      .video-item {
        width: 100%;
        border: 5px solid #000;
        box-sizing: border-box;
        .iframe-box {
          width: 100%;
          padding-bottom: 100%;
          position: relative;
          iframe {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0; } }
        .info {
          padding: 4.6875% 5.46875%;
          .sub-title {
            color: $red;
            @include fontH3; }
          .txt {
            @include fontH3; } } } } } }


